  background-color: #f0f8ff;
  flex-direction: column;
  flex-basis: calc(33.3333% - 30px);
  margin-bottom: 30px;
  margin-right: 30px;
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
